<template>
  <div>
    <button type="button" @click="foo1">toast1</button>
    <button type="button" @click="foo2">toast2</button>
    <button type="button" @click="foo3">toast3</button>
  </div>
</template>

<script>
export default {
  name: 'Toast',
  methods: {
    foo1(){
      this.$toast('1')
    },
    foo2(){
      this.$toast({
        message: '2',
        iconClass: 'success'
      })
    },
    foo3(){
      this.$toast({
        message: '3',
        iconClass: 'fail',
        duration: 500
      })
    }
  }
}
</script>
